export function GameboardFactory(name) {
    const hits = [];
    const misses = [];
    const occupied = [];
    const receiveAttack = (coord) => {
        if (occupied.includes(coord))
            hits.push(coord);
        else
            misses.push(coord);
    };
    const allSunk = () => {
        return occupied.length === hits.length;
    };
    const newShip = (coords) => {
        if (coords.some((coord) => occupied.includes(coord)))
            return "Coordinate(s) already occupied";
        else
            occupied.push(...coords);
    };
    return {
        name,
        hits,
        misses,
        occupied,
        receiveAttack,
        allSunk,
        newShip,
    };
}
