import { GameboardFactory } from "./factories/gameboard";
import { PlayerFactory } from "./factories/player";
import { startEventListener, createComputerGrid, renderMovableBoats, addBoatEventListeners, renderComputerBoard, infoPlayerMove, addSquareEventListeners, renderPlayerBoard, } from "./DOM";
startEventListener();
export function createPlayersAndBoards() {
    const player = PlayerFactory();
    const computer = PlayerFactory();
    const playerBoard = GameboardFactory("Player");
    const computerBoard = GameboardFactory("Computer");
    return { player, computer, playerBoard, computerBoard };
}
export function initGame() {
    const { player, computer, playerBoard, computerBoard } = createPlayersAndBoards();
    renderMovableBoats(player);
    addBoatEventListeners();
}
export function startBattle(player, computer, playerBoard, computerBoard) {
    createComputerGrid(computer, computerBoard);
    renderPlayerBoard(playerBoard);
    renderComputerBoard(computerBoard);
    infoPlayerMove();
    addSquareEventListeners((e) => player.playerMove(e, computerBoard));
}
